<template>
  <div class="container">
    <div class="row mb-3" v-for="(posts, index) in postRows" :key="'posts-'+index">
      <div v-for="(post) in posts" :key="post.guid" :class="colClass">
        <div class="post-date">{{ post.pubDate | formatPostDate }}</div>
        <div><a :href="post.link" target="_blank" :style="'background-image: url(' + post.thumbnail + ');'" class="post-image"></a></div>
        <div class="author mt-2">Author: {{ post.author }}</div>
        <div class="excerpt" v-html="formatPostDescription(post.description)">
        </div>
        <div class="read-more"><a :href="post.link" target="_blank">Read more</a></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import moment from 'moment'

export default {
  name: 'Grid',
  computed: {
    ...mapState([
      'posts',
      'maxCols',
      'layout',
      'postRows',
      'colClass'
    ])

  },
  filters: {
    formatPostDate: function(value) {
      if (value) {
          return moment(String(value)).format('Do MMM')
      }
    },

  },
  methods: {
    formatPostDescription: (value) => {
      console.log('value=',value);
      return value.replace(/<img[^>]*>/g,"");
    }
  }
}
</script>

