<template>
  <div id="rtb">
    <router-view />
  </div>
</template>

<script>
export default {
  props: [
    'rss',
    'maxcols',
    'layout'
  ],
  created () {
    console.log('created', this.maxcols)
    /**
     * kick off the store
     */
    this.$store.commit('setRss', this.rss)
    this.$store.commit('setMaxCols', this.maxcols)
    this.$store.commit('setLayout', this.layout)
    this.$store.dispatch('getPosts', this.rss)
  }
}
</script>

